.li,
a {
  color: rgba(31, 103, 199, 1);
}

.inputContainer {
  display: flex;
  max-width: 19.5rem;
}

.input {
  flex-grow: 1;
  height: 2rem;
  border: 1px solid #eeeeee;
  box-sizing: border-box;
}

.inputButton {
  padding: 0 1rem;
  height: 2rem;
  border: 0px;
  background: #eeeeee;
  cursor: pointer;
}
